import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Typist from 'react-typist';
import * as firebase from 'firebase';

class Home extends Component {
    constructor(){
        super();
        this.state = {
             speed: []
        };
    }

    componentDidMount(){
         const rootRef = firebase.database().ref().child('react');
         const speedRef = rootRef.child('projects');

         speedRef.on('value', snap => {
             this.setState({speed: snap.key})
         });
    }
    render() {
        return ( 
    <div>
            <header id="home-section" style={{ background: 'url("imgs/mural.jpg") no-repeat', backgroundPosition: 'center'}}>
                <div className="dark-overlay">
                    <div className="home-inner">
                        <div className="container">
                            <div className="row no-gutters mx-auto">
                            <Typist>
                               <h2 className="col-12 text-center pb-5">Hello! I'm Luis.<br/> I'm an Experienced Full Stack Web Developer and also an aspiring Environmental Scientist</h2>  
                               <a href="#home"><button className="btn btn-outline-info">Learn more about me</button></a>
                            </Typist>
                            </div>
                        </div>
                    </div>
                </div>
            </header>  
            <div id="home" >
            <div className="card card-body bg-info text-white section-padding pt-5">
            <img className="img-fluid rounded-cirlce header-image mx-auto d-block" src="imgs/me.jpg" alt="Me"/>
                <h2>Welcome to my page {this.state.speed}</h2>
                <p className="lead">I am an aspiring environmental scientist and full stack web developer. I love learning and challenging myself to learn new technologies, and implementing technology to help sustainable development in Belize. I currently hold a bachelors degree in Environmental Science with a background in computer science from Galen University. I am also a full stack, self-taught web developer; however, polished my programming skills by taking computer science classes while studying at Galen University. I hope to one day make I impactful applications that can help the environment across the world.</p>
            </div>
            <div className="container py-5">
                <h3>My Skills</h3>
                <p className="lead">Over the past few years, with my degree and passions, I have develop the skills below: </p> 
                <hr/>
                <h4>HTML</h4>
                <div className="progress mb-3">  
                     <div className="progress-bar bg-info" style={{width: 100+"%"}}></div>
                </div> 
                <h4>CSS</h4>
                <div className="progress mb-3">  
                     <div className="progress-bar bg-info" style={{width: 100+"%"}}></div>
                </div> 
                <h4>JavaScript</h4>
                <div className="progress mb-3">  
                     <div className="progress-bar bg-info" style={{width: 95+"%"}}></div>
                </div> 
                <h4>Jquery</h4>
                <div className="progress mb-3">  
                     <div className="progress-bar bg-info" style={{width: 80+"%"}}></div>
                </div> 
                <h4>React.js</h4>
                <div className="progress mb-3">  
                     <div className="progress-bar bg-info" style={{width: 90+"%"}}></div>
                </div> 
                <h4>Express.js</h4>
                <div className="progress mb-3">  
                     <div className="progress-bar bg-info" style={{width: 80+"%"}}></div>
                </div> 
                <h4>Mongodb</h4>
                <div className="progress mb-3">  
                     <div className="progress-bar bg-info" style={{width: 90+"%"}}></div>
                </div> 
                <h4>Angular</h4>
                <div className="progress mb-3">  
                     <div className="progress-bar bg-info" style={{width: 80+"%"}}></div>
                </div> 
                <h4>C++</h4>
                <div className="progress mb-3">  
                     <div className="progress-bar bg-info" style={{width: 70+"%"}}></div>
                </div> 
                <h4>WordPress</h4>
                <div className="progress mb-3">  
                     <div className="progress-bar bg-info" style={{width: 95+"%"}}></div>
                </div> 
                <Link className="p-2"  to="/resume"><button className="btn btn-outline-info"> See where I Aquired these skills</button></Link> 
                <Link className="p-2" to="/portfolio"><button className="btn btn-outline-info"> See my skills in Action</button></Link>
            </div>
        </div>
        </div>
        );
    }
}

export default Home;


