import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import * as firebase from 'firebase';

class Login extends Component {
    constructor(props){
        super(props);
        this.state = {
            email: '',
            password: '',
            redirect: false
        }
    this.handleEmail = this.handleEmail.bind(this);
    this.handlePassword = this.handlePassword.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleEmail(event) { 
        this.setState({email: event.target.value});
      }
     handlePassword(event) { 
        this.setState({password: event.target.value});
      }
    
      handleSubmit(event) { 
        event.preventDefault();
        firebase.auth().signInWithEmailAndPassword(this.state.email, this.state.password)
        .then(() => {
            this.setState({redirect: true});
        })
        .catch((error) => {
            // Handle Errors here.
            var errorCode = error.code;
            var errorMessage = error.message;
            // ...
          });
      }
 

       
    render() {
        if(this.state.redirect === true ){
            return <Redirect to="/dashboard" />
        }
        return (
            <div className="login">
                 <form className="form-signin" onSubmit={this.handleSubmit}>
                    <img className="mb-4" src="/imgs/me.jpg" alt="" width="72" height="72"/>
                    <h1 className="h3 mb-3 font-weight-normal">Please sign in</h1>
                    <label htmlFor="inputEmail" className="sr-only">Email address</label> 
                    <input type="email" id="inputEmail" className="form-control" placeholder="Email address"  value={this.state.email} onChange={this.handleEmail}  />
                    <label htmlFor="inputPassword" className="sr-only">Password</label> 
                    <input type="password" id="inputPassword" className="form-control" placeholder="Password" value={this.state.password} onChange={this.handlePassword}  />
                    <div className="checkbox mb-3">
                        <label>
                        <input type="checkbox" value="remember-me"/> Remember me
                        </label>
                    </div>
                    <button className="btn btn-lg btn-info btn-block" type="submit">Sign in</button> 
                </form> 
            </div>
        );
    }
}

export default Login;


