import React, { Component } from 'react';
import * as firebase from 'firebase';

class Contact extends Component {
    constructor(props){
        super(props);
        this.state = {
            name: '',
            email: '',
            message: ''
        }

        this.getFormVals = this.getFormVals.bind(this); 
    }
    

    getFormVals(e){
        e.preventDefault(); 
        const itemsRef = firebase.database().ref('items');
       const item = {
        name: this.state.name,
        email: this.state.email,
        message: this.state.message
      }
      itemsRef.push(item);
        this.setState({
            name: '',
            email: '', 
            message: ''
        });
        document.getElementById("alert").style.display = "block";
        setTimeout(() => {
            document.getElementById("alert").style.display = "none";
        }, 4000);
    }

    render() {
        return (
            <div id="contact">
                    <div className="card card-body bg-info section-padding pb-5">
                        <h2>Contact</h2>
                    </div>
                    <div className="card card-body py-5">
                    <img className="img-fluid rounded-cirlce header-image mx-auto d-block animated wobble" src="imgs/me.jpg" alt="Me"/>
                        <h3>Get in Touch</h3>
                        <p className="lead animated slideInDown">The best way to get in contact with me is through my Email: <a href="#">manzanero.luis1995@gmail.com</a>. You can also use the form below to reach out to me directly to my email. I hope that you're convinced, Thank you!</p>
                        <div className="alert alert-primary" id="alert" style={{display: "none"}} role="alert">Your message has been sent</div>
                        <form onSubmit={this.getFormVals} id="main-form" className="animated zoomInDown">
                           <div className="form-group">
                               <div className="input-group input-group-lg">
                                   <span className="input-group-addon bg-info text-white">
                                       <i className="fa fa-user"></i>
                                   </span>
                                   <input type="text" className="form-control bg-dark text-white" value={this.state.name} onChange={(e) => this.setState({name: e.target.value})} placeholder="Name" required/>
                               </div>
                           </div> 
                           <div className="form-group">
                                <div className="input-group input-group-lg">
                                    <span className="input-group-addon bg-info text-white">
                                        <i className="fa fa-envelope"></i>
                                    </span>
                                    <input type="email" className="form-control bg-dark text-white" value={this.state.email} onChange={(e) => this.setState({email: e.target.value})} placeholder="Email" required/>
                                </div>
                            </div> 
                            <div className="form-group">
                               <div className="input-group input-group-lg">
                                   <span className="input-group-addon bg-info text-white">
                                       <i className="fa fa-pencil"></i>
                                   </span>
                                   <textarea className="form-control bg-dark text-white" value={this.state.message} onChange={(e) => this.setState({message: e.target.value})} placeholder="Message"></textarea>
                               </div>
                           </div> 
                           <input type="submit" value="submit" className="btn btn-info btn-block btn-lg"/>
                        </form>
                    </div>
                </div>
        );
    }
}

export default Contact;


